.taskForm {
    width: 100%;
    font-family: 'Roboto';
    display: flex;
    justify-content: center;
    flex-direction: column;
    flex-wrap: wrap;
}

.taskForm .taskTitle{
    display: block;
    border: none;
    width: 50%;
    border-bottom: 1px solid black;
    padding: 10px;
    outline: none;
    margin: auto;
    font-size: 2em;
    text-align: center;
}

.taskForm .taskDescription {
    display: block;
    border: none;
    width: 75%;
    border-bottom: 1px solid black;
    padding: 10px;
    outline: none;
    margin: auto;
    margin-top: 30px;
    font-size: 1.2em;
    font-family: 'Roboto';
    height: 100px;
}

input:hover{
    border-bottom: 2px solid black;
    transition: all ease-out 1s;
}

.customDatePicker {
    margin: auto;
    margin-top: 25px;
    width: 65%;
    text-align: center;
}

.taskCompleteButton{    
    margin: auto;
    margin-top: 20px;
    margin-bottom: 20px;
    background: none;
    border: 2px solid #656565;
    border-color: #656565;
    border-radius: 10rem;
    padding: 10px 30px;
    font-size: 1.2em;
    color: #656565;
    outline: 0;
}

.taskCompleteButton:hover{
    background-color: #00CD00	;
    color: #ffffff;
    border-color: #00CD00;
    font-weight: bold;
}

.modalCloseBtn {
    color: crimson;
    width: fit-content;
    margin-left: auto;
    font-weight: bolder;
    font-size: 1.5em;
    font-stretch: expanded;
    background: none;
    outline: none;
    border: none;
}

.modalCloseBtn:hover{
    cursor: pointer;
}