.miniCard{
    width: 270px;
    height: 230px;
    border-radius: 15px;
    background: #ffffff;    
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    margin: 20px;
    padding: 20px;
    transition: all ease 0.15s;
}

.miniCard:hover {
    transition: all ease 0.3s;    
    transform-origin: 50% 50%;
    transform: scale(1.05);
    cursor: pointer;
}

.cardTitle{
    color: #656565;
    text-align: center;
    font-weight: bold;
    font-size: 1.3em;
    margin: 0;
    margin-bottom: 8px;
}

.cardDescription {
    height: 50%;
    padding: 8px;
    color: #656565;
    overflow: hidden;
    font-size: 1.1em;
    text-overflow: ellipsis;
}

.cardDueDateTime {
    padding: 8px;
    color: #656565;
}

